import React from 'react'
import SimpleReactFooter from "simple-react-footer"; 

export default function reactfooter() {

    const description = "National Irrigation Administration - Chico River Pump Irrigation Project. Website is maintained by CRPIP's Information Technology team. ";
    const title = "About";
    const columns = [
      {
          title: "GOV PH",
          resources: [
              {
                  name: "GOV.PH",
                  link: "http://www.gov.ph/"
              },
              {
                  name: "Open Data Portal",
                  link: "http://www.gov.ph/data"
              },
              {
                  name: "Official Gazette",
                  link: "http://www.officialgazette.gov.ph/"
              }
          ]
      },
      
      {
          title: "Government Links",
          resources: [
              {
                  name: "Office of the President",
                  link: "http://president.gov.ph/"
              },
              {
                  name: "Senate of the Philippines",
                  link: "http://www.senate.gov.ph/"
              },
              {
                name: "House of Representatives",
                link: "http://www.congress.gov.ph/"
            },
            {
              name: "Supreme Court",
              link: "http://sc.judiciary.gov.ph/"
          },
          {
            name: "Court of Appeals",
            link: "http://ca.judiciary.gov.ph/"
        },
        {
          name: "Sandiganbayan",
          link: "http://sb.judiciary.gov.ph/"
      }
          ]
      }
   ];

    return (
        <SimpleReactFooter 
        description={description} 
        title={title}
        columns={columns}
        copyright="NIA - CRPIP"
        iconColor="white"
        backgroundColor="black"
        fontColor="green"
        copyrightColor="darkgrey"
     />
    )
}
